import { Component } from '@angular/core';
import { IonicPage,  NavParams, ViewController, App } from 'ionic-angular';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';

@IonicPage( { name: 'LoadingPage' })
@Component({
  selector: 'page-loading-modal',
  templateUrl: 'loading.html',
})
export class LoadingPage {
 
  constructor(public appCtrl: App, public navParams: NavParams, public viewCtrl: ViewController,
   public globalVars: GlobalVarsProvider ) {
   
  }

  ionViewDidLoad() {
    //console.log('ionViewDidLoad ConfirmModal');
  }


  closeModal() {
    //console.log();
    this.viewCtrl.dismiss();
  }

 


}



